.qc-input {
  display: inline-block;
  position: relative;
  vertical-align: middle;
  width: 100%;
}
.qc-input_warp__prepend {
  display: inline-table;
}
.qc-input_warp__append {
  display: inline-table;
}
.qc-input_inner {
  display: inline-block;
  width: 100%;
  height: 40px;
  padding: 4px 10px;
  font-size: 14px;
  border: 1px solid #e5e5e5;
  color: #999999;
  background-color: #ffffff;
  background-image: none;
  position: relative;
  cursor: text;
  outline: none;
  box-sizing: border-box;
}
.qc-input_inner__textarea {
  resize: none;
}
.qc-input_inner__radius {
  border-radius: 4px;
}
.qc-input_inner:focus, .qc-input_inner:hover {
  border: 1px solid #1c96ff;
}
textarea.qc-input_inner {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
  padding: 5px 10px;
  line-height: 1.5;
}
.qc-input_inner__disable {
  background-color: #f3f3f3;
  opacity: 1;
  cursor: not-allowed;
  color: #cccccc;
}
.qc-input_inner__normal {
  height: 32px;
  padding: 4px 10px;
  font-size: 12px;
}
.qc-input_inner__disable:hover {
  border: 1px solid #e5e5e5;
}
.qc-input_inner__small {
  height: 24px;
  padding: 1px 10px;
  font-size: 12px;
}
.qc-input_icon {
  display: inline-block;
  speak: none;
  font-style: normal;
  font-weight: 400;
  font-variant: none;
  text-transform: none;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}
.qc-input_icon__large {
  width: 40px;
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  text-align: center;
  position: absolute;
  right: 0;
  z-index: 3;
}
.qc-input_icon__normal {
  width: 32px;
  height: 32px;
  line-height: 32px;
  font-size: 14px;
}
.qc-input_icon__small {
  width: 24px;
  height: 24px;
  line-height: 24px;
  font-size: 12px;
}
.qc-input_icon + .qc-input_inner {
  padding-right: 40px;
}
.qc-input_icon + .qc-input_inner__normal {
  padding-right: 32px;
}
.qc-input_icon + .qc-input_inner__small {
  padding-right: 24px;
}
.qc-input_group {
  font-size: inherit;
  font-variant: 400;
  line-height: 1;
  color: #666666;
  text-align: center;
  background: #f8f8f8;
  border: 1px solid #e5e5e5;
  border-radius: 4px;
  width: 1px;
  white-space: nowrap;
  vertical-align: middle;
  display: table-cell;
}
.qc-input_group_append {
  border-left: 0;
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
  background: #ffffff;
  color: #666666;
}
.qc-input_group_prepend {
  border-right: none;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}
.qc-input_warp__append .qc-input_inner {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-right: none;
}
.qc-input_warp__prepend .qc-input_inner {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-left: none;
}
.qc-input_warp .qc-input_inner {
  border-radius: 0;
  border-left: none;
  border-right: none;
}
.qc-input_warp__small .qc-input_group {
  font-size: 12px;
}
.qc-input_warp__normal .qc-input_group {
  font-size: 14px;
}
.qc-input_warp__append .qc-input_inner:focus {
  border: 1px solid #e5e5e5;
  border-right: none;
}
.qc-input_warp__prepend .qc-input_inner:focus {
  border: 1px solid #e5e5e5;
  border-left: none;
}
.qc-input_warp__append .qc-input_inner:hover {
  border: 1px solid #e5e5e5;
  border-right: none;
}
.qc-input_warp__prepend .qc-input_inner:hover {
  border: 1px solid #e5e5e5;
  border-left: none;
}
.qc-input_warp .qc-input_inner:hover, .qc-input_warp .qc-input_inner:focus {
  border-radius: 0;
  border-left: none;
  border-right: none;
}



