
.qc-scrollbar{
  width: 100%;
  position: relative;
  overflow: hidden;
}
.qc-scrollbar_bar{
  position: absolute;
  width: 6px;
  right: 2px;
  bottom: 0;
  top: 0;
  z-index: 1;
  border-radius: 4px;
}
.qc-scrollba_thumb{
  position: absolute;
  display: block;
  width: 100%;
  height: 20%;
  cursor: pointer;
  border-radius: inherit;
  background-color: rgba(151,168,190,.3);
  opacity: 0;
}
.qc-scrollbar:hover .qc-scrollba_thumb {
  opacity: 1;
}



