.pagination {
  display: inline-block;
  margin: 20px 0;
  padding-right: 10px;
  font-size: 14px;
  line-height: 38px;
  color: #282828;
  overflow: hidden;
}
.pagination_prev {
  display: block;
  float: left;
  margin-right: 5px;
  width: 78px;
  text-align: center;
  cursor: pointer;
  border: 1px solid #cccccc;
  color: #282828;
  background-color: #ffffff;
}
.pagination_next {
  display: block;
  float: left;
  margin-right: 5px;
  width: 78px;
  text-align: center;
  cursor: pointer;
  border: 1px solid #cccccc;
  color: #282828;
  background-color: #ffffff;
}
.pagination_prev:hover {
  border: 1px solid #125fa0;
}
.pagination_next:hover {
  border: 1px solid #125fa0;
}
.pagination__disabled {
  background-color: #f5f5f5;
  cursor: default;
  color: #bcbcbc;
  border: 1px solid #e5e5e5;
}
.pagination__disabled:hover {
  border: 1px solid #e5e5e5;
}
.pagination_ul_ellipsis {
  display: block;
  float: left;
  margin-right: 5px;
  font-size: 16px;
  text-align: center;
}
.pagination_turn {
  float: left;
  margin: 0 15px;
}
.pagination_turn_span {
  display: block;
  float: left;
  margin-right: 5px;
}
.pagination_turn_input {
  float: left;
  width: 38px;
  height: 38px;
  margin: 0 5px;
  text-align: center;
  line-height: 38px;
  padding: 0;
  border: 1px solid #cccccc;
}
.pagination_turn_input:focus {
  border: 1px solid #125fa0;
}
.pagination_btn {
  display: block;
  float: left;
  margin-right: 5px;
  width: 78px;
  text-align: center;
  line-height: 38px;
  background: #125fa0;
  color: #ffffff;
  cursor: pointer;
}
.pagination .btn:hover {
  border: 1px solid #125fa0;
}
.pagination_ul {
  float: left;
  margin: 0;
  padding: 0;
  list-style: none;
}
.pagination_ul__list {
  float: left;
  width: 38px;
  margin-right: 5px;
  text-align: center;
  cursor: pointer;
  color: #282828;
  border: 1px solid #cccccc;
  background-color: #ffffff;
}
.pagination_ul__list:hover {
  border: 1px solid #125fa0;
}
.pagination_ul__active {
  background: #125fa0;
  color: #ffffff;
}
.pagination_ul__active:hover {
  border: 1px solid #125fa0;
}



