
.qc-modal_mark[data-v-31a111b0]{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  z-index: 1000;
  background-color: rgba(55,55,55,.6);
}
.qc-modal_wrap[data-v-31a111b0]{
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 1000;
  overflow: auto;
  outline: 0;
  text-align: center;
}
.qc-modal_wrap[data-v-31a111b0]:after{
  content: "";
  display: inline-block;
  height: 100%;
  width: 0;
  vertical-align: middle;
}
.qc-modal_inner[data-v-31a111b0]{
  position: relative;
  display: inline-block;
  width: auto;
  text-align: left;
  vertical-align: middle;
  background-color: #fff;
}
.qc-modal_header[data-v-31a111b0]{
  border-bottom: 1px solid #e5e5e5;
  background-color: #f9f9f9;
  padding: 14px 16px;
  line-height: 1;
  position: relative;
}
.qc-modal_close[data-v-31a111b0]{
  position: absolute;
  right: 12px;
  top: 14px;
}
.qc-modal_close_inner[data-v-31a111b0]{
  display: inline-block;
  position: relative;
  top: -4px;
  width: 14px;
  height: 14px;
  margin-left: 6px;
  cursor: pointer;
  overflow: hidden;
}
.qc-modal_header_title[data-v-31a111b0]{
  font-size: 14px;
  color: #333333;
}
.qc-modal_content[data-v-31a111b0]{
  padding: 16px;
  line-height: 1.5;
}
.qc-modal_content_slot[data-v-31a111b0]{
  font-size: 14px;
  color: #666666;
}
.qc-modal_footer[data-v-31a111b0]{
  //background-color: #f8f8f8;
  padding: 20px 18px;
}
.qc-modal_close_inner[data-v-31a111b0]:after,.qc-modal_close_inner[data-v-31a111b0]:before{
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  top: 50%;
  left: 0;
  margin-top: -2px;
  background-color: #666;
}
.qc-modal_close_inner[data-v-31a111b0]:before{
  transform: rotate(45deg)
}
.qc-modal_close_inner[data-v-31a111b0]:after{
  transform: rotate(-45deg)
}
.qc-ease-enter-active[data-v-31a111b0],.qc-ease-leave-active[data-v-31a111b0]{
  animation-duration: .5s;
  animation-fill-mode: both;
}
.qc-ease-enter-active[data-v-31a111b0]{
  animation-name:fadeInDown;
}
.qc-ease-leave-active[data-v-31a111b0]{
  animation-name:fadeOutUp;
}
@-webkit-keyframes fadeInDown{
from {
    opacity: 0;
    transform: translate3d(0, -40%, 0);
}
to {
    opacity: 1;
    transform: none;
}
}
@keyframes fadeInDown {
from {
    opacity: 0;
    transform: translate3d(0, -40%, 0);
}
to {
    opacity: 1;
    transform: none;
}
}
@-webkit-keyframes fadeOutUp{
from {
    opacity: 1;
}
to {
    opacity: 0;
    transform: translate3d(0, -40%, 0);
}
}
@keyframes fadeOutUp {
from {
    opacity: 1;
}
to {
    opacity: 0;
    transform: translate3d(0, -40%, 0);
}
}



