.qc-select_dropdown_list_item[data-v-5d3f7f85] {
  display: block;
  margin: 0;
  line-height: normal;
  padding: 7px 16px;
  color: #666666;
  white-space: nowrap;
  list-style: none;
  cursor: pointer;
  font-size: 14px;
}
.qc-select-dropdown_list__disabled[data-v-5d3f7f85] {
  color: #e5e5e5;
  cursor: not-allowed;
}
.qc-select_dropdown_list_item[data-v-5d3f7f85]:hover {
  background-color: #f3f3f3;
}
.qc-select-dropdown_list__disabled[data-v-5d3f7f85]:hover {
  color: #e5e5e5;
  background-color: transparent;
}
.qc-select_dropdown_list__selected[data-v-5d3f7f85] {
  color: #ffffff;
  background-color: #0098ff;
}
.qc-select_dropdown_list__selected[data-v-5d3f7f85]:hover {
  color: #ffffff;
  background-color: #0098ff;
}
.qc-select_dropdown_list__focus[data-v-5d3f7f85] {
  background-color: #f3f3f3;
}
.qc-select_multiple .qc-select_dropdown_list__selected[data-v-5d3f7f85] {
  background-color: #ffffff;
  position: relative;
  color: #0098ff;
}
.qc-select_multiple .qc-select_dropdown_list__selected[data-v-5d3f7f85]:after {
  content: "";
  display: inline-block;
  width: 4px;
  height: 8px;
  line-height: 1;
  border: 2px solid #0098ff;
  border-top: 0;
  border-left: 0;
  transform: rotate(45deg);
  position: absolute;
  right: 8px;
  top: 50%;
  margin-top: -6px;
}
.qc-select_multiple .qc-select_dropdown_list__selected[data-v-5d3f7f85]:hover {
  background-color: #ffffff;
  color: #0098ff;
