  background-color: #f3f3f3;
}
.qc-select .qc-select_dropdown_list__selected.qc-select_dropdown_list__focus[data-v-5d3f7f85] {
  background-color: #125fa0;
}
.qc-select_multiple .qc-select_dropdown_list__selected.qc-select_dropdown_list__focus[data-v-5d3f7f85] {
