  border-right: 6px solid transparent;
  border-top: 6px solid #ccc;
  position: absolute;
  right: 8px;
  top: 50%;
  margin-top: -3px;
  transition: all .2s ease-in-out;
}
.qc-select_clear[data-v-aea6dee8] {
  display: none;
  width: 12px;
  height: 12px;
  line-height: 12px;
  text-align: center;
  position: absolute;
  right: 8px;
