  margin: 0;
}
[data-v-aea6dee8]::-webkit-input-placeholder {
  color: #999999;
}
[data-v-aea6dee8]::-moz-placeholder {
  color: #999999;
}
[data-v-aea6dee8]:-ms-input-placeholder {
  color: #999999;
}
input[data-v-aea6dee8]:-moz-placeholder {
  color: #999999;
}
.qc-select__disabled .qc-select_input[data-v-aea6dee8]::-webkit-input-placeholder {
