}
.slide-up-enter-active[data-v-aea6dee8] {
  opacity: 0;
  animation-timing-function: ease-in-out;
}
.slide-up-leave-active[data-v-aea6dee8] {
  animation-timing-function: ease-in-out;
}
.slide-up-enter-active[data-v-aea6dee8], .slide-up-leave-active[data-v-aea6dee8] {
  animation-duration: .3s;
  animation-fill-mode: both;
  animation-play-state: paused;
}
.slide-up-appear[data-v-aea6dee8], .slide-up-enter-active[data-v-aea6dee8] {
  animation-name: qcSlideUpIn;
  animation-play-state: running;
}
.slide-up-leave-active[data-v-aea6dee8] {
  animation-name: qcSlideUpOut;
  animation-play-state: running;
}
@-webkit-keyframes qcSlideUpIn {
0% {
    opacity: 0;
    transform-origin: 0% 0%;
    transform: scaleY(0.8);
}
100% {
    opacity: 1;
    transform-origin: 0% 0%;
    transform: scaleY(1);
}
}
@keyframes qcSlideUpIn {
0% {
    opacity: 0;
    transform-origin: 0% 0%;
    transform: scaleY(0.8);
}
100% {
    opacity: 1;
    transform-origin: 0% 0%;
    transform: scaleY(1);
}
}
@-webkit-keyframes qcSlideUpOut {
0% {
    opacity: 1;
    transform-origin: 0% 0%;
    transform: scaleY(1);
}
100% {
    opacity: 0;
    transform-origin: 0% 0%;
    transform: scaleY(0.8);
}
}
@keyframes qcSlideUpOut {
0% {
    opacity: 1;
    transform-origin: 0% 0%;
    transform: scaleY(1);
}
100% {
    opacity: 0;
    transform-origin: 0% 0%;
    transform: scaleY(0.8);
}
}



