
.qc-swiper{
  position: relative;
  height: 100%;
}
.qc-swiper .qc-swiper_carousel{
  position: relative;
  overflow: hidden;
}
.qc-swiper_indicators {
  padding: 0;
  margin: 0;
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
}
.qc-swiper_indicator {
  display: inline-block;
  /*padding: 0 10px;*/
  color: #fff;
}
.qc-swiper_button{
  display: inline-block;
  width: 12px;
  height: 12px;
  margin: 0 10px;
  background: #fff;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s;
  opacity: 0.5;
}
.qc-swiper_button:hover{
  background: #ffffff;
  opacity: 1;
}
.qc-swiper_button__active{
  width: 24px;
  height: 12px;
  border-radius: 6px;
  opacity: 1;
}
.qc-swiper_prev{
  font-size: 60px;
  color: #fff;
  position: absolute;
  left: 5px;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.5s;
  cursor: pointer;
}
.qc-swiper_next{
  font-size: 60px;
  color: #fff;
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.5s;
  cursor: pointer;
}
.qc-swiper_arrow_left-enter,
.qc-swiper_arrow_left-leave-active {
  /*transform: translateY(-50%) translateX(0px);*/
  opacity: 0;
}
.qc-swiper_arrow_right-enter,
.qc-swiper_arrow_right-leave-active {
  /*transform: translateY(-50%) translateX(10px);*/
  opacity: 0;
}



